.Nav {
	background-color: violet;
	color: white;
	text-shadow: black 0 0 1px;
	position: fixed;
	height: 100%;
	top: 0;
	width: 15vw;
	transform: translateX(-100%);
	transition: transform 1s;
	padding: 1.5rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	ul {
		height: 50vh;
		display: flex;
		justify-content: space-around;
		flex-direction: column;
		margin: 4rem 0;

		li {
			list-style-type: none;
		}
		&:hover {
			color: black;
		}
	}
}
.a {
	transform: translateX(0);
}
