	height: 15vh;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 5em;
	color: white;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
	text-shadow: black 0 0 5px;
	h1 {
		font-size: 3rem;
	}
}
.bird {
	font-size: 4em;
}
.burger {
	font-size: 2.5em;
	cursor: pointer;
}
