.Birds {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	h1 {
		margin: 1.5rem;
	}
	img {
		width: 150%;
	}
}
.profile {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 1.5rem;
	h3 {
		margin: 1.5rem;
	}
	svg {
		font-size: 100px;
		margin: 1.5rem;
		cursor: pointer;
	}
	img {
		border-radius: 25%;
		margin: 1.5rem;
	}
}
.image-icons {
	display: flex;
	justify-content: center;
	align-items: center;
}
